.nav {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
}

.nav__menu {
    padding: 1rem;
    width: 25rem;
    background: #fff;
    color: rgba(0, 0, 0, 0.87);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    -webkit-transform: translateX(-12.5rem);
        -ms-transform: translateX(-12.5rem);
            transform: translateX(-12.5rem);
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    z-index: 1;
    font-size: 1rem;
    line-height: 1rem;
    /* padding: 3rem; */
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: auto;
}

.nav__item--selected {
    background: rgba(0,0,0,0.1);
}

.nav__menu li + li {
    padding-top: 1rem;
}

.tabs .tab {
    text-transform: none;
}

.nav-wrapper {
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}
.nav-wrapper, .nav__left {
    display: -ms-flexbox;
    display: flex;
}

.material-icons:hover {
    cursor: pointer;
}

.nav__toggle {
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 40px;
    height: 40px;
}

.nav__toggle_icon {
    padding: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.nav__toggle_icon.material-icons {
    font-size: 36px;
    line-height: 36px;
}


